@use "variables";

.button {
  padding: 12px;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  white-space: nowrap;

  &__primary {
    color: #fff;
    background-color: variables.$purple;

    @media (max-width: 640px) {
      display: block;
      width: 100%;
     
    }
    @media (max-width:1024px){
      display: flex;
      align-items: center;
    }
  }
}
